.form__group:last-child {
  margin-bottom: 0;
}
@media all and (min-width: 501px) {
  .form__group .form__field {
    margin-right: 20px;
  }
}
.form__group .form__field:last-child {
  margin-right: 0;
}
.form__field {
  flex-grow: 1;
}
.form__field.disabled {
  opacity: 0.8;
  pointer-events: none;
}
.form__field-card {
  width: 100%;
  display: flex;
}
.form__field-card .input-card {
  flex-grow: 1;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
.form__field-card .input-exp {
  width: 37%;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  text-align: center;
}
.form__field-code {
  position: relative;
  width: 100%;
  display: flex;
  margin: 10px -5px 0;
}
.form__field-code input {
  width: calc(16.6666666667% - 10px);
  margin: 0 5px;
  padding: 10px;
  text-align: center;
  height: 69px;
  font-size: 20px;
  font-weight: 500;
}
@media all and (max-width: 640px) {
  .form__field-code input {
    width: calc(16.6666666667% - 5px);
    margin: 0 2.5px;
    height: auto;
    aspect-ratio: 1;
  }
}
@media all and (max-width: 480px) {
  .form__field-code input {
    font-size: 14px;
  }
}
.form__field-cvv {
  max-width: 90px;
}
.form__field-cvv input {
  text-align: center;
}
.form__field label {
  display: flex;
  flex-wrap: wrap;
}
.form__field-label {
  width: 100%;
  margin-bottom: 10px;
  font-size: 14px;
  color: rgb(138, 148, 166);
  color: var(--color-text);
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 500;
}
.form__field-error {
  font-size: 12px;
}
.form__field--error .form__field-label {
  color: rgb(255, 82, 82);
  color: var(--color-danger);
}
.form__field--error input {
  border-color: rgb(255, 82, 82) !important;
  border-color: var(--color-danger) !important;
  color: rgb(255, 82, 82) !important;
  color: var(--color-danger) !important;
}
.form__publicOffer {
  color: rgb(138, 148, 166);
  color: var(--color-text);
  text-align: center;
  margin-top: 20px;
  line-height: 20px;
  font-size: 14px;
}
@media all and (max-width: 480px) {
  .form__publicOffer {
    font-size: 12px;
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 31px;
  flex-wrap: wrap;
}
@media all and (max-width: 640px) {
  .footer {
    flex-direction: column;
    row-gap: 15px;
    margin-top: 20px;
  }
}
.footer a {
  color: rgb(3, 0, 91);
  color: var(--color-secondary);
}
@media all and (max-width: 640px) {
  .footer a {
    font-size: 14px;
  }
}

@keyframes rotating {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.page-loader {
  position: absolute;
  top: 0;
  left: 30px;
  right: 0;
  bottom: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
.page-loader svg {
  animation: rotating 1.5s linear infinite;
}`, "",{"version":3,"sources":["webpack://./src/styles/variables/_variables.scss","webpack://./src/styles/main.scss","webpack://./src/styles/variables/_colors.scss","webpack://./src/styles/_mixins.scss","webpack://./src/styles/_fonts.scss","webpack://./src/styles/_reset.scss","webpack://./src/styles/_global.scss","webpack://./src/styles/modules/_button.scss","webpack://./src/styles/modules/_modal.scss","webpack://./src/styles/modules/_drawer.scss","webpack://./src/styles/modules/_languageSwitcher.scss","webpack://./src/styles/modules/_header.scss","webpack://./src/styles/modules/_input.scss","webpack://./src/styles/modules/_form.scss","webpack://./src/styles/modules/_footer.scss","webpack://./src/styles/modules/_pageLoader.scss"],"names":[],"mappings":"AAAA;EACE,kDAAA;EACA,gDAAA;EAEA,oBAAA;EACA,oBAAA;EACA,oBAAA;EACA,oBAAA;EAEA,wBAAA;EAEA,iBAAA;EACA,iBAAA;EACA,iBAAA;EAEA,iBAAA;ACHF;;ACZA;EACE,gCAAA;EACA,0CAAA;EAEA,0BAAA;EACA,0CAAA;EAEA,gCAAA;EACA,8CAAA;EAEA,+BAAA;EACA,kDAAA;EAEA,+BAAA;EACA,wCAAA;EAEA,gCAAA;EACA,0CAAA;EAEA,iCAAA;EACA,4CAAA;EAEA,+BAAA;EACA,wCAAA;EAEA,qCAAA;EACA,oDAAA;EAEA,+BAAA;EACA,4CAAA;ADMF;;AEAE;EACE,yBAAA;EACA,kBCrCyE;EDsCzE,gBCtCoE;EDuCpE,uOAPM;AFUV;AEPE;EACE,yBAAA;EACA,kBCpCwE;EDqCxE,gBCrCmE;EDsCnE,uOAPM;AFgBV;AEbE;EACE,yBAAA;EACA,kBCnCsE;EDoCtE,gBCpCiE;EDqCjE,yOAPM;AFsBV;AItDA;EACE,SAAA;EACA,UAAA;EACA,sBAAA;AJwDF;;AIrDA;EACE,kBAAA;AJwDF;;AIrDA;EACE,aAAA;EACA,SAAA;EACA,gBAAA;EACA,6BAAA;AJwDF;;AKtEA;EACE;IACE,wBAAA;ELyEF;EKvEA;IACE,yCAAA;ELyEF;AACF;AKtEA;EACE,oBAAA;EAAA,6BAAA;EACA,aAAA;EACA,sBAAA;EACA,oBAAA;EACA,iBAAA;EACA,6DAAA;EAAA,+BAAA;ALwEF;;AKrEA;EACE,uBAAA;EAAA,2BAAA;EACA,gBAAA;EACA,qBAAA;EAAA,mCAAA;EACA,6BAAA;EAAA,qBAAA;ALwEF;AKtEE;EACE,kCAAA;EAAA,0BAAA;ALwEJ;;AKpEA;EACE,wBAAA;ALuEF;;AKnEE;EACE,gBAAA;ALsEJ;;AKlEA;EACE,aAAA;EACA,sBAAA;EACA,iBAAA;EACA,iEAAA;EACA,kBAAA;ALqEF;AKnEE;EACE,kBAAA;ALqEJ;;AKjEA;EACE,gBAAA;EAAA,iCAAA;EACA,YAAA;EACA,WAAA;ALoEF;;AKjEA;EACE,gBAAA;ALoEF;;AM9HA;EACE,kBAAA;EACA,mBAAA;EAAA,+BAAA;EACA,eAAA;EACA,kBAAA;EACA,g